@import url("https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap");

* {
  margin: 0;
  padding: 0
}

body {
  font-family: Inter, sans-serif;
  background: #f8fafc;
}

:root {
  --primary-color:#606cbc;
  --secondary-color: #f60;
  --border-radius: 20px;
  --main-bg-color: #30bfa1
}

a {
  text-decoration: none
}

.mt-4 {
  margin-top: 10px
}

.w-90 {
  width: 90%
}

.screens {
  margin-left: 20px;
  margin-right: 20px
}

.space-btn {
  display: flex;
  justify-content: space-between
}

.font-md {
  color: #111;
  font-size: 30px;
  font-weight: 600
}

.font-sm {
  color: #111;
  font-size: 18px;
  font-weight: 400
}

.span {
  color: #606cbc
}

button.medium {
  background-color: var(--primary-color);
  border: none;
  border-radius: 19px;
  color: #fff;
  outline: none;
  padding: 10px
}

.button-transparent {
  background: #0000;
  border: 1px solid gray;
  border-radius: 19px;
  color: #111;
  cursor: pointer;
  font-size: 15px;
  font-weight: 600;
  padding: 9px;
  width: auto
}

.input__error input {
  border-color: red
}

.error__feedback {
  color: red
}

.additional-image {
  height: auto;
  width: 200px
}

.scrollable-div {
  max-height: 100vh;
  overflow-y: auto;
}

.pt-70 {
  padding-top: 80px;
}

.scroll-container {
  max-height: 400px;
  overflow-y: scroll;
}

.mw-90 {
  max-width: 90%;
}

::-webkit-scrollbar {
  display: none;
}

main{
  margin-top: 110px;
}

@media (max-width: 768px) {
  main {
    margin-top: 80px;
  }
}